* {
    box-sizing: border-box;
}

#testConfig{
    --horizontalPadding: 1em;
    --verticalPadding: 1em;
    display: grid
;
    font-size: .95rem;
    gap: .5em;
    grid-auto-flow: column;
    height: -moz-max-content;
    height: max-content;
    width: 100%;
    transition: opacity .125s;
    justify-content: space-around;
    margin-bottom: 1rem;
}

#testConfig .row {
    display: flex;
    background: var(--sub-alt-color);
    border-radius: var(--roundness);
}

#testConfig .puncAndNum {
    justify-content: start;
}

button.text, .button.text, .textButton {
    color: aqua;
    cursor: pointer;
    transition: background .125s, color .125s, opacity .125s;
    padding: .5em;
    border-radius: var(--roundness);
    background: var(--bg-color);
    text-align: center;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    align-content: center;
    height: -moz-min-content;
    height: min-content;
    line-height: 1.25;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border: none;
    font-family: inherit;
    font-size: 1em;
    display: inline-flex
;
    gap: .5em;
    align-items: baseline;
    justify-content: center;
    
}

#testConfig .spacer {
    height: auto;
    width: .5em;
    border-radius: calc(var(--roundness) / 2);
    background: var(--bg-color);
    margin: .75em 0;
    transition: .25s;
}

#testConfig .mode, #testConfig .time, #testConfig .wordCount, #testConfig .puncAndNum, #testConfig .quoteLength, #testConfig .customText, #testConfig .zen {
    display: grid
;
    grid-auto-flow: column;
}
#testConfig .spacer {
    height: auto;
    width: .5em;
    border-radius: calc(var(--roundness) / 2);
    background: var(--bg-color);
    margin: .75em 0;
    transition: .25s;
}

#testConfig .time, #testConfig .wordCount, #testConfig .customText, #testConfig .zen, #testConfig .quoteLength {
    justify-content: end;
}
#testConfig .mode, #testConfig .time, #testConfig .wordCount, #testConfig .puncAndNum, #testConfig .quoteLength, #testConfig .customText, #testConfig .zen {
    display: grid
;
    grid-auto-flow: column;
}
#testConfig .mode .textButton, #testConfig .time .textButton, #testConfig .wordCount .textButton, #testConfig .puncAndNum .textButton, #testConfig .quoteLength .textButton, #testConfig .customText .textButton, #testConfig .zen .textButton {
    padding: var(--verticalPadding) var(--horizontalPadding);
}

#testConfig .mode .textButton:first-child, #testConfig .time .textButton:first-child, #testConfig .wordCount .textButton:first-child, #testConfig .puncAndNum .textButton:first-child, #testConfig .quoteLength .textButton:first-child, #testConfig .customText .textButton:first-child, #testConfig .zen .textButton:first-child {
    margin-left: var(--horizontalPadding);
}
#testConfig .mode .textButton, #testConfig .time .textButton, #testConfig .wordCount .textButton, #testConfig .puncAndNum .textButton, #testConfig .quoteLength .textButton, #testConfig .customText .textButton, #testConfig .zen .textButton {
    padding: var(--verticalPadding) var(--horizontalPadding);
}

a {
    text-decoration: none;
    color: inherit; /* Removes the underline */
  }
  a:hover{
    color: rgb(255, 1, 196);
  }
  